@use "../abstracts/variables.scss";
@use "../abstracts/mixins";

.flex-container {
  @include mixins.flexContainer();
}

.sec {
  @include mixins.sectionMix();
  margin-bottom: 40px;
}

.sec h1 {
  @include mixins.marginTopBottom(60px, 30px);
}

.hiddenOne,
.hiddenTwo {
  overflow: hidden;
}

@media all and (max-width: 1000px) {
  .sec {
    width: 80%;
  }
}
@media all and (max-width: 500px) {
  .sec {
    width: 90%;
  }
  .flex-container {
    width: 95%;
    margin: 0 auto;
  }
}
